import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import '@vkontakte/vkui/dist/vkui.css';
import { FormLayout, FormLayoutGroup, Input, Select, Checkbox, Link, Title, Header, Spinner } from '@vkontakte/vkui';
import "../layout/css/file.css"
import {useDispatch} from "react-redux";

const Home = props => {
	const vkParam=window.location.search
    const [userLogin, setUserLogin]=React.useState('');
    const [isLoaded, setIsLoaded]=React.useState(false);
    const [userPassword, setUserPassword]=React.useState('');
    const [userSchool, setUserSchool]=React.useState({name: "", vendor: ""});
    const [userVend, setUserVend]=React.useState('');
    const [source, setSource]=React.useState('1');
    const [massage, setMassage]=React.useState('');
    const [showSchools, setShowSchools]=React.useState(false);
    const dropdown=React.useRef();
    const [schools, setSchools]=React.useState([]);
    const change=(data)=>{
        fetch("https://likeee.online/api2/login.php?source=3&q="+data+"&search=1").then(response=>response.json())
            .then((result)=>{
                setSchools(getdata(result, data));
            }).catch(error=>{throw new Error("Ошибка: " + error)});
        setUserSchool(data);
    }
    function getdata(result, data) {
        if (!data)
            return []
        const items=result.data.map(item=>{
            return {
                name: item.name,
                vendor: item.vendor,
                region: item.name2,
            }
        })
        return items
    }

	function changeDiary(data) {
        if (data==3){
        	console.log(data);
            setShowSchools(true);
        } else setShowSchools(false);
        setSource(data);
    }
	return (
		<Panel id={props.id}>
			<PanelHeader>Авторизация</PanelHeader>
			<FormLayout>
				<FormLayoutGroup top="Дневник">
		      		<Select id="source_sh" onChange={(event) => changeDiary(event.target.value)} >
					  <option value="1" >school.mosreg.ru</option>
					  <option value="2" >dnevnik.ru</option>
					  <option value="3" >eljur.ru</option>
					</Select>
				</FormLayoutGroup>
					      			{showSchools&&<>
					    <FormLayoutGroup top="Выберите номер школы:">
				        <Input className="input" value={userSchool.name} onChange={(event) => {
					            change(event.target.value)
					        }} onFocus={() => {
					            dropdown.current.classList.add("active")
					        }} onBlur={() => {
					            setTimeout(() => {
					                dropdown.current.classList.remove("active")
					            }, 100);
					        }} placeholder="Введите название школы"/>
					        </FormLayoutGroup>
				        <div className="dropdown" ref={dropdown}>
				        {schools.length &&
				        <>
				            {schools.map((school, index) => (
				                <div onClick={(event) => {
				                    setUserSchool(school)
				                    setUserVend(school.vendor)
				                }} key={index}><div>{school.name}</div><div>{school.region}</div></div>
				            ))}
				        </>
				        }
				        </div>
				        </>
				        }
	      		<FormLayoutGroup top="Авторизация">
			        <Input id="login_sh" type="text" placeholder="Логин" />
			        <Input id="password_sh" type="password" placeholder="Пароль" />
			        <input id="vend" type="hidden" value={userVend}/>
	      		</FormLayoutGroup>
	    	</FormLayout>
			<Group title="Navigation Example">
				<Div>
					<Button size="xl" level="2" onClick={props.goo}>
						Войти
					</Button>
				</Div>
			</Group>
			<Div>
				<Title id="lol2" level="3" weight="semibold" style={{ marginBottom: 16 }}>{props.fetch_log}</Title>
			</Div>
		</Panel>
	);
}
//<Checkbox id="legal_sh">Я согласен со всем, что вы <Link href="https://google.com" target="_blank">там</Link> понаписали</Checkbox>
export default Home;